"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { cn } from "@/lib/utils"
import {
  LayoutDashboard,
  Users,
  Calendar,
  Activity,
  BookOpen,
  MapPin,
  UserCheck,
  FileText,
  UserPlus,
} from "lucide-react"

const navigation = [
  { name: "Dashboard", href: "/", icon: LayoutDashboard },
  { name: "Clubs", href: "/clubs", icon: Users },
  { name: "Events", href: "/events", icon: Calendar },
  { name: "Activities", href: "/activities", icon: Activity },
  { name: "Students", href: "/students", icon: BookOpen },
  { name: "Resources", href: "/resources", icon: MapPin },
  { name: "Memberships", href: "/memberships", icon: UserCheck },
  { name: "Reservations", href: "/reservations", icon: FileText },
  { name: "Guests", href: "/guests", icon: UserPlus },
]

export function AppSidebar() {
  const pathname = usePathname()

  return (
    <aside className="fixed left-0 top-0 z-40 h-screen w-64 border-r border-border bg-card">
      <div className="flex h-full flex-col">
        {/* Logo/Brand */}
        <div className="flex h-16 items-center border-b border-border px-6">
          <h2 className="text-2xl font-bold text-foreground">cl0v manger</h2>
        </div>

        {/* Navigation */}
        <nav className="flex-1 space-y-1 overflow-y-auto px-3 py-4">
          {navigation.map((item) => {
            const isActive = pathname === item.href
            return (
              <Link
                key={item.name}
                href={item.href}
                className={cn(
                  "flex items-center gap-3 rounded-lg px-3 py-2.5 text-sm font-medium transition-all",
                  isActive
                    ? "bg-primary text-primary-foreground shadow-sm"
                    : "text-muted-foreground hover:bg-muted hover:text-foreground",
                )}
              >
                <item.icon className="h-5 w-5" />
                {item.name}
              </Link>
            )
          })}
        </nav>

        {/* Footer */}
        <div className="border-t border-border p-4">
          <p className="text-xs text-muted-foreground text-center">Campus Management System</p>
        </div>
      </div>
    </aside>
  )
}
